import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';

export const sendContactForm = createAsyncThunk(
  'contact/sendContactForm',
  async (formData: any, thunkAPI) => {
    try {
      const res = await fetch('/api/contact_us', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(formData)
      });

      const data = await res.json();
      if (!res.ok) throw new Error(data.message || 'Error sending message');

      return data.message;
    } catch (error: any) {
      return thunkAPI.rejectWithValue(error.message);
    }
  }
);

const contactSlice = createSlice({
  name: 'contact',
  initialState: {
    loading: false,
    successMessage: '',
    errorMessage: ''
  },
  reducers: {
    resetStatus: (state) => {
      state.successMessage = '';
      state.errorMessage = '';
    }
  },
  extraReducers: (builder) => {
    builder
      .addCase(sendContactForm.pending, (state) => {
        state.loading = true;
      })
      .addCase(sendContactForm.fulfilled, (state, action) => {
        state.loading = false;
        state.successMessage = action.payload;
      })
      .addCase(sendContactForm.rejected, (state, action) => {
        state.loading = false;
        state.errorMessage = action.payload as string;
      });
  }
});

export const { resetStatus } = contactSlice.actions;
export default contactSlice.reducer;
